import React from 'react';


class Post extends React.Component {

    constructor(props){
        super(props)
        this.state = {
            fullname: "",
            feedback:""
        }

        this.captureName = this.captureName.bind(this)
        this.captureFeedback = this.captureFeedback.bind(this)
    }

    //event object is always received as a paramater when working with events in JS.
    captureName = function(event){
        //console.log("onChange called");
        console.log(event);
        console.log(event.target);
        console.log(event.target.value);
        this.setState({fullname: event.target.value})
    }
    captureFeedback = function(event){
        //console.log("onChange called");
        console.log(event);
        console.log(event.target);
        console.log(event.target.value);
        this.setState({feedback: event.target.value})
    }


    render() {
        return (
            <div>
                <b>Form to post update!!!!</b>
                <p>
                    Name: <input type="text" onChange={this.captureName}></input>
                </p>
                <p>
                    Feedback: <textarea rows="5" cols="17" onChange={this.captureFeedback}></textarea>
                </p>
                <p>
                    Name: {this.state.fullname}
                    <br></br>
                    Feedback:{this.state.feedback}
                </p>

            </div>
        );
    }
}

export default Post;