import React from 'react';
import axios from "axios";
import SingleUser from './singleuser';


class Users extends React.Component {

    constructor(props) {
        super(props)
        this.state = {
            allusers: []
        }

    }



    componentWillMount() {
        this.loadRemoteUsers()
        //alert()
    }

    loadRemoteUsers = function () {
        //axios.get will make a call to rest api. 
        // there will be 2 scenarios as response comes back
        // 1. success (in this case response will contain the data)
        // 2. error (in thi case response will contain the error message)
        //success scenario is dealt with in then() function
        //error scenario is dealt with in catch() function
        axios.get("https://jsonplaceholder.typicode.com/users")
            .then(response => {
                console.log("Response received: ");
                console.log(response);
                console.log(response.data);
                console.log("Length of an array: ");
                console.log(response.data.length);
                console.log("Print any individual JSON object from response:");
                console.log(response.data[0]);
                console.log(response.data[0].website);
                console.log(response.data[0].address.geo.lat);
                console.log("Value of state allusers: ");
                console.log(this.state.allusers);
                //initialize allusers state with response.data
                this.setState({
                    allusers: response.data
                })
                console.log("Value of state allusers after initialization: ");
                console.log(this.state.allusers);
            })
            .catch(error => {
                console.log("Error received:");
                console.log(error);
            })

    }

    renderAllUsers = function () {
        return this.state.allusers.map(user => {
            console.log(user.name);
            return (
                <SingleUser
                    key={user.id}
                    myname={user.name}
                    latitude={user.address.geo.lat}
                    longitude={user.address.geo.lng}
                ></SingleUser>
            )
        })
    }

    renderAllUsersInTable = function () {
        return this.state.allusers.map(user => {
            return (
              
                    <SingleUser
                        key={user.id}
                        myname={user.name}
                        latitude={user.address.geo.lat}
                        longitude={user.address.geo.lng}
                    ></SingleUser>
              
            )
        })
    }


    render() {
        return (
            <div>
                Load users from REST API! Call REST API with help of axios.
                <br></br>
                API: https://jsonplaceholder.typicode.com/users
                <p>
                    {this.renderAllUsers()}
                </p>
                <table border="1">
                    <thead>
                        <tr>
                            <th>Name</th>
                            <th>Latitude</th>
                            <th>Longitude</th>
                        </tr>
                    </thead>
                    <tbody>
                        {this.renderAllUsersInTable()}
                    </tbody>

                </table>
            </div>
        );
    }
}

export default Users;