import React from 'react';
import { Route, Switch } from 'react-router-dom';
import Contact from '../pages/contact';
import Home from '../pages/home';
import Post from '../pages/post';
import Users from '../pages/remote/users';
import Services from '../pages/services';


class Content extends React.Component {
    render() { 
        return (
        <div>
            <Switch>
                <Route exact path="/" component={Home}></Route>
                <Route path="/rba-services" component={Services}></Route>
                <Route path="/rba-contact" component={Contact}></Route>
                <Route path="/post-update" component={Post}></Route>
                <Route path="/call-api" component={Users}></Route>
            </Switch>            
        </div>
        );
    }
}
 
export default Content;