import React from 'react';
import { Link } from 'react-router-dom';

class Menu extends React.Component {

    liStyle = {
        display:"inline", 
        padding:"5px"
        
    }

    render() { 
        return (
        <div>
           <ul style={{listStyleType:"none"}}>
               <li style={this.liStyle}><Link to="/">Home</Link></li>
               <li style={this.liStyle}><Link to="/rba-services">Services</Link></li>
               <li style={this.liStyle}><Link to="/rba-contact">Contact</Link></li>
               <li style={this.liStyle}> <Link to="/post-update">Post</Link></li>
               <li style={this.liStyle}> <Link to="/call-api">Remote Users</Link></li>
                

           </ul>
        </div>
        );
    }
}
 
export default Menu;